import React from 'react';
import renderHTML from 'react-render-html';
import { ListItemWrapper } from './style';
import { slugify } from '../../../../services/functions';
import { Link } from 'react-router-dom';

const SearchResult = ({ title, body, children, getCategory, id }) => (
    <ListItemWrapper>
        <Link to={`/articles/${slugify(title, id)}`} className='text-16 base-line-height text-semibold text-title'>{ title }</Link>
        <p className='text-15 text-normal text-black70 bs-mg-bottom'> 
        <Link className="text-12 category-link" to={getCategory().url}>
            {getCategory().name}
        </Link>
        <br/>
        {renderHTML(body)}
        </p>
        <hr className='no-margin border-black20 lg-mg-bottom'/>
    </ListItemWrapper>
)

export default SearchResult;


