import styled from 'styled-components';
import { sc_media } from '../../utils';

export const PaginationWrapper = styled.nav `
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding-left: 1em;
    padding-right: auto;
    padding-top: 1em;
    padding-bottom: 1em;

    ul {
        padding-left: 1.1em;
        padding-right: 1.1em;
        display: flex;
    }

    ${sc_media.phone `
        padding-left: 0.4em;
        padding-right: 0.4em;

        ul {
            padding-left: 0.6em;
            padding-right: 0.6em;
        }
    `}
`

export const PaginationItem = styled.li `
    width: 26px;
    height: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    list-style: none;
    border-radius: 100%;
    margin: 0 0.6em;
    cursor: pointer;

    &.active {
        color: white;
        background-color: #1372EF;
        :hover {
            background-color: #1372EF; 
        }
    }

    :hover {
        background-color: #EBF6FD;
    }

    ${sc_media.phone `
        margin: 0 0.1em;
    `}
`

export const PaginationNavigation = styled.span `
    height: 26px;

    button:first-child {
        margin-right: 0.6em;
    }

    button {
        border: 0;
        background-color: transparent;
        width: 26px;
        height: 26px;
        border-radius: 100%;
        cursor: pointer;

        :hover {
            background-color: #EBF6FD;
        }

        &:focus {
            outline: 0;
        }
    }

    .icon {
        font-size: 11px;
    }

    ${sc_media.phone `
        button:first-child {
            margin-right: 0.1em;
        }
    `}
`




