// Package Import
import React from 'react';
import { withRouter } from 'react-router';
import URLSearchParams from 'url-search-params';
import { connect } from 'react-redux';
import * as Scroll from 'react-scroll';
import { defaultCategories } from '../../constants/globalVariable';
// Component import
import SEO from '../../components/layouts/SEO';
import DesktopNavigation from '../../components/layouts/Header/DesktopSecondaryNav';
import SectionHomeFourth from '../../components/commons/Sections/Section-Home-4';
import SectionHomeFifth from '../../components/commons/Sections/Section-Home-5';
import Pagination from '../../components/commons/Pagination';
import ListSearch from '../../components/commons/Lists/ListItemSearch';
import { SingleSkeleton, ParagraphSkeleton } from '../../components/commons/SkeletonBar';
import { ButtonBlue } from '../../components/commons/Buttons/Button';
import Breadcrumb from '../../components/commons/Breadcumbs';
import { ContentTrees } from '../../components/commons/Articles/ContentTrees';

// Functions Import 
import { getAllSection, searchResult } from '../../services/api';
import { setAllSection, changeSearchResults, changeSearchQuery } from '../../actions';
import { loadAllData } from '../../services/functions';
import queryString from 'query-string';

const Events     = Scroll.Events;
class SearchResult extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            loading: true, 
            totalPages: 0,
            nowPosition: 0,
            currentPage: 1,
            perPage: 10,
            error: false
        }

        this._callToSearch = this._callToSearch.bind(this);
        this._sortingPage = this._sortingPage.bind(this);
        this._renderPagination = this._renderPagination.bind(this)
    }

    componentDidMount(){
        Events.scrollEvent.register('begin');
        Events.scrollEvent.register('end');
        Scroll.scrollSpy.update();
    }

    async componentWillMount(){
        try {
            if (!this.props.articles.get('sections').size)
                await loadAllData(getAllSection ,'sections')(data => {
                    this.props.setAllSection(data);
                });

            await this._callToSearch((data) => {
                this.props.changeSearchResults(data)
                this.props.changeSearchQuery(this._getCurrentParameter().query)
            });
            this._sortingPage();
        } catch(e) {
            this.setState({error: true})
        }
    }

    shouldComponentUpdate(nextProps, nextState){
        console.log('next propsnya', nextProps);
        console.log('next state', nextState);
        return true;
    }

    async componentWillReceiveProps(nextProps){
        Scroll.animateScroll.scrollToTop({duration: 300});
        try {
            if (this.props.articles.toJS().searchWord !== new URLSearchParams(nextProps.location.search).get('q')){
                this.setState({ loading : true })
                await this._callToSearch((data) => {
                    this.props.changeSearchResults(data)
                    this.props.changeSearchQuery(this._getCurrentParameter().query)
                })
             }else {
                let pageQuery = new URLSearchParams(nextProps.location.search).get('page')
                this.setState({currentPage : pageQuery === null ? 1 : Number(pageQuery)})
             }
        }catch(e) {
            this.setState({error: true})
        }
        
    }

    componentWillUnmount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
      }

    _sortingPage(){
        const { searchArticles } = this.props.articles.toJS();
        const { currentPage, perPage } = this.state;   

        let lastIndex = currentPage * perPage;
        let firstIndex = lastIndex - perPage;
        return {
            lastIndex,
            firstIndex
        }
    }


    async _callToSearch(callback){

        //Call Api To Search Articles
        await loadAllData(searchResult, 'results', queryString.stringify({query: this._getCurrentParameter().query}))
                         ((data) => {
                            callback(data);
                            // jika query routing page melebihi dari pagination yang ada atau m
                             if (Math.ceil(data.length / this.state.perPage) < this._getCurrentParameter().page || data.length === 0 ) {
                                this.setState({error: true})
                             }else {
                                this.setState({error: false})
                             }
                             setTimeout(() => {
                                this.setState({loading : false})
                            }, 1000)
                         },
                          e => console.log(e));
    }

    _getCurrentParameter(){
        return {
            query: new URLSearchParams(this.props.location.search).get('q'),
            page: new URLSearchParams(this.props.location.search).get('page')
        }
    }

    _getCategory(sectionId) {
        const categoryId = Object.values(this.props.articles.toJS().sections).find((item, index) => sectionId === item.id).category_id;
        return {
            name: defaultCategories.find(value => value.id == categoryId).name,
            url: `/categories/${categoryId}`
        }
    }

    _renderPagination(){
        if (this.state.error) {
            return (
                <div className='align-center sp-pd-top'>
                    <img src={require('../../assets/help-1.svg')} alt='not found image'/>
                    <h5 class='text-semibold text-18 bs-mg-top'>No results for "{ this._getCurrentParameter().query}"</h5>
                    <ButtonBlue className='lg-mg-top bs-mg-bottom' onClick={() => this.props.history.push('/')}>Browse knowledge base</ButtonBlue>
                </div>
            )
        }
        else{
            return (
                <div>
                    <SectionHomeFifth>
                        <h3 className="no-mg text-18 text-semibold text-black70">Search results</h3>
                        <p className="base-line-height no-mg margin-bottom text-12">{ this.props.articles.toJS().searchArticles.length } results for 
                            <span className="result-keyword">"{ this._getCurrentParameter().query }"</span>
                        </p>
                    </SectionHomeFifth>
                    <hr className="border-black20 bs-mg-top" />
                    <div className="container container--in-mobile-medium bs-pd-lr bs-pd-lr--only-desktop lg-mg-bottom">
                        <ul className='list-nostyle'>
                            {
                                this.props.articles.toJS().searchArticles.slice(this._sortingPage().firstIndex, this._sortingPage().lastIndex).map(value => 
                                    <ListSearch
                                    key={value.id}
                                    title={value.title}
                                    body={value.snippet}
                                    id={value.id}
                                    getCategory={() => this._getCategory(value.section_id)}
                                    />)
                            }
                        </ul>
                    </div>
                    <Pagination 
                        totalPage={Math.ceil(this.props.articles.toJS().searchArticles.length / this.state.perPage)|| 1}
                        currentPage={this.state.currentPage}
                        handleClick={(num) => {
                            this.setState({ currentPage: num})
                            this.props.history.push(`/articles/search?${queryString.stringify({q: this._getCurrentParameter().query})}&&page=${num}`)
                            Scroll.animateScroll.scrollToTop({duration: 300});
                        }}
                    />
                </div>
            )
        }
    }




    render(){
        return (
            <div>
                <SEO
                    schema={'http://127.0.0.1:3000'}
                    title={`Pencarian - Kitabisa Help Center`}
                    description={`Pusat bantuan - Cari seputar Artikel yang dapat membantu anda`}
                    path={`http://127.0.0.1:3000`}
                    contentType={`text/html; charset=utf-8`}
                    category={'Search'}
                    tags={`search, pencarian`}
                />
                <main className='js-header-sticky'>
                    <DesktopNavigation categoryId={''}/>
                    <section className='no-border'>
                        <div className='container'>
                            <Breadcrumb crumb={
                                [{
                                    name: 'Search results',
                                    url: ''
                                }]
                            }/>
                            <ContentTrees>
                                <div className='bg-white bs-pd-top sp-pd-bottom full-width br-4'>
                                    {
                                        this.state.loading ?
                                        <div className='lg-pd-top lg-pd-lr'>
                                            <SingleSkeleton width={'230px'} className='lg-mg-bottom' darken />
                                            <ParagraphSkeleton />
                                        </div> :
                                        this._renderPagination()
                                    }
                                </div>
                            </ContentTrees>
                        </div>
                    </section>
                </main>
            </div>   
        )
    }
}

const mapStateToProps = (state) => {
    return { articles : state.article }
};
  
const mapDispatchToProps = (dispatch) => ({
    setAllSection: (payload) => dispatch(setAllSection(payload)),
    changeSearchResults: (payload) => dispatch(changeSearchResults(payload)),
    changeSearchQuery: (payload) => dispatch(changeSearchQuery(payload))
})


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SearchResult));


