import React from 'react';
import { PaginationWrapper, PaginationItem, PaginationNavigation } from './style';
import { getRange } from '../../../services/functions';


const _getPagination = (position, totalPage) => {
    try {
        let firstPage = position - 1 > 4 ? getRange(position - 4, 4): getRange(1, position - 1)
        let lastPage = position + 4 > totalPage ?  getRange(position + 1, totalPage - position) : getRange(position + 1, 4);   
        return [...firstPage, Number(position), ...lastPage]; 
    }catch(e) {
        return [0, 1];
    }
}

 const Pagination = ({ currentPage, totalPage, handleClick }) => (
        <PaginationWrapper>
            {
                currentPage !== 1 && 
                        <PaginationNavigation>
                            <button onClick={() => handleClick(1)}><span className='icon anticon icon-doubleleft'></span></button>
                            <button onClick={() => handleClick(currentPage - 1)}><span className='icon anticon icon-left'></span></button>
                        </PaginationNavigation>
            }
            <ul>
            { _getPagination(currentPage, totalPage)
                  .map(( val, index ) => 
                    <PaginationItem className={Number(val) === Number(currentPage) && 'active'} key={index}
                        onClick={() => handleClick(val)}>
                        { val }
                    </PaginationItem>)}
            </ul>
            {
                currentPage !== totalPage && <PaginationNavigation>
                    <button onClick={() => handleClick(currentPage + 1)}><span className='icon anticon icon-right'></span></button>
                    <button onClick={() => handleClick(totalPage)}><span className='icon anticon icon-doubleright'></span></button>
                </PaginationNavigation>
            }
        </PaginationWrapper>
 )


export default Pagination;


