import styled from 'styled-components';
import { sc_media } from '../../../utils';

export const ListItemWrapper = styled.li `
    padding-top: 0.3em;

    .text-title {
        margin: 0.8em 0 0 0;
        color: #414141;
        font-weight: normal;

        &:hover {
            text-decoration: underline;
        }
    }

    p {
        margin-top: 0;
        margin-bottom: 1.5em;
        line-height: 1.6em;
        color: #737373;
        em {
            font-weight: 600;
            padding: 3px 5px;
            font-style: normal;
            margin-right: 4px;
            border-radius: 2px;
            color: #5F5C5C;
        }
    }

    hr {
        margin-top: 1.5em;
    }

    .category-link {

    }
`


